.sidecard {
  background-color: var(--background-color);
  color: var(--text-color);
  align-items: center;
  text-align: center;

  box-shadow: 5px 5px var(--shadow-color);

  padding: 5%;
  margin-bottom: 2em;
}

.themer-row {
  position:sticky;
  top: 2em;
  flex-direction: row-reverse;
  margin-left: 0 !important;
}

#themer {
  background-color: var(--next-background-color);
  color: var(--next-text-color);
  align-items: center;
  text-align: center;
  
  float: right;
  box-shadow: 5px 5px var(--next-shadow-color);
  
  padding: 1.5vh;
  font-size: 1.5em;
  margin-bottom: 0.5em;
}

#themer:hover {
  transform: translateY(-0.25em);
}

#themer:active {
  transform: translateY(0.5em);
}

.content-card {
  background-color: var(--background-color);
  color: var(--text-color);
  box-shadow: 10px 10px var(--shadow-color);
  
  padding: 4vh 2vh;
  margin-bottom: 0.8em;
}

.content-card-title {
  font-weight: bolder;
  font-size: 1.5em;
  margin-bottom: 1.5em;
  margin-left: 0.5em;
}

.content-card-content {
  line-height: 1.5em;
  font-size: 1em;
}

.content-card-content a {
  color: var(--text-color);
  font-weight: 600;
}

.content-card-subtitle,
.content-card-subtitle-displaced {
  font-size: 0.8em;
  margin-top: 0.6em;
  margin-bottom: 0.6em;
}

.content-list {
  margin-bottom: 1.5em;
}

.content-link {
  font-size: 0.8em;
}

.right-float {
  float: right;
}

.placard-image {
  width: 90%;
  box-shadow: 5px 5px var(--shadow-color);
}

.placard-title {
  padding-top: 1em;
  font-size: 2em;
  font-weight: bolder;
}

.placard-role {
  margin-top: 0.25em;
}

.placard-location {
  margin-top: 0.25em;
  font-weight: bold;
}

/* physical lines within cards */
.liner {
  border: 1px solid var(--shadow-color);
}

.social {
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  margin-left: 1em;
  margin-right: 1em;
}

.social a{
  color: var(--text-color);
  font-size: 1em;
}

.social a:hover,
.content-card a:hover {
  color: var(--shadow-color);
  font-weight: bold;
}

.social a:last-child{
  margin-right: 0;
}

.footer {
  background-color: var(--shadow-color);
  color: var(--background-color);
  height: 10vh;

  padding: 3vh;
  text-align: center;
}

.plain-jane,
#bg-source {
  color: var(--background-color) !important;
}

.plain-jane a:hover,
#bg-source a:hover {
  text-decoration: none;
}

#bg-source {
  font-size: 0.6em;
}

@media (min-width: 1280px) {
  .sidecard {
    position:sticky;
    top: 2em;
    width: 100%;
    box-shadow: 10px 10px var(--shadow-color);
  }

  .social a {
    font-size: 0.7em;
  }

  .placard-title {
    font-size: 1em;
  }

  .placard-role {
    font-size: 0.8em;
  }

  .placard-location {
    font-size: 0.6em;
  }

  .content-card {
      box-shadow: 10px 10px var(--shadow-color);
  }

  .content-card-title {
    font-size: 1.2em;
    margin-bottom: 0.5em;
  }

  .content-card-content {
    font-size: 0.8em;
    margin-left: 0.5em;
  }

  .content-card-subtitle-displaced {
    margin-left: 1em;
  }

  .right-float {
    margin-right: 2em;
  }

  .possibly-empty-link {
    font-size: 0.7em;
  }

  .themer-row {
    width: 100%;
  }

  #themer{
    padding: 1vh;
    font-size: 1em;
  }

  .footer {
      font-size: 0.7em;
  }
}